import React from 'react'
import { DAppProvider, Config } from '@usedapp/core'

const config: Config = {
  readOnlyChainId: Number(process.env.REACT_APP_NETWORK_ID ?? 2152),
  readOnlyUrls: {
    2152: 'https://prod-mainnet.prod.findora.org:8545',
    2153: 'https://prod-testnet.prod.findora.org:8545',
  },
}

const DAppWrapper: React.FC = ({ children }) => (
    <DAppProvider config={config}>
      { children }
    </DAppProvider>
)

export default DAppWrapper


