import { makeStyles } from '@material-ui/core'
import { useEthers } from '@usedapp/core'

import { useEffect, useState } from 'react'
import {
  displayEllipsedAddress,
  networkID,
} from 'utils/helpers'
import configs from 'configs'

import Button from '.'

const useStyles = makeStyles((theme) => ({
  connect: {
    color: 'white',
  },
  connected: {
    backgroundColor: 'transparent',
    color: 'white',
    '&:hover': {
      backgroundColor: 'transparent',
    },
    boxShadow: 'inherit',
  },
}))

type ConnectStatus = 'connect' | 'switchNetwork' | 'wrongNetwork' | 'connected'

export default function ConnectButton({ className }: { className?: string }) {
  // const [walletButtonText, setwalletButtonText] = useState('Connect')
  const [connectStatus, setStatus] = useState<ConnectStatus>('connect')
  const classes = useStyles()
  const {
    account,
    error,
    activateBrowserWallet,
    switchNetwork,
    deactivate,
  } = useEthers()
  // const {
  //   error: walletError,
  //   status,
  //   account,
  //   setisConnectWalletsModal,
  //   reset,
  //   connectByNetwork,
  //   currentConnectionType,
  // } = useWalletWrapper()

  const isSwitchNetworkSupported = true
    // (!isMobileOrHub || window.ethereum?.isImToken) &&
    // currentConnectionType === 'injected'
  // || window.ethereum?.isMetaMask
  const network =
    networkID === '2153' ? 'Findora Testnet' : 'Findora Mainnet'

  const isSwitchNetwork = error && error.name === 'ChainIdError'
    // isSwitchNetworkSupported &&
    // walletError &&
    // walletError.message.includes('Unsupported chain')

  const getConnectButtonText = () => {
    switch (connectStatus) {
      case 'connected':
        return displayEllipsedAddress(account)
      case 'switchNetwork':
        return `Switch to ${network}`
      case 'wrongNetwork':
        return 'Wrong network'
      default:
        return 'connect'
    }
  }

  const handleConnectModal = () => {
    if (!account) {
      if (isSwitchNetwork) {
        switchNetwork(configs.network.chainId)
      } else {
        activateBrowserWallet()
        // setisConnectWalletsModal(true)
      }
    } else {
      deactivate()
    }
  }

  useEffect(() => {
    if (account) {
      setStatus('connected')
    } else if (
      // wrong network
      error &&
      error.message.includes('Unsupported chain')
    ) {
      isSwitchNetworkSupported
        ? setStatus('switchNetwork')
        : setStatus('wrongNetwork')
    } else {
      setStatus('connect')
    }
  }, [account, error, isSwitchNetworkSupported])

  return (
    <Button
      className={
        account && !isSwitchNetwork
          ? `${className} ${classes.connected}`
          : className || classes.connect
      }
      onClick={handleConnectModal}
    >
      {getConnectButtonText()}
    </Button>
  )
}
